.section-search-form {

  background: #f4f4f6 url(../images/section-search-bg.jpg) 50% 0 no-repeat;
  -webkit-background-size: cover;
  background-size: cover;

  .section-container {
    padding: 110px 0 130px;
  }

  .section-content {
    max-width: 1240px;
  }

  .section-title {
    font-size: 42px;
    text-transform: uppercase;
    line-height: 1;
    padding-top: 100px;
    margin: 0 0 40px 0;
    position: relative;

    &:before {
      content: '';
      display: block;
      position: absolute;
      background: #a2d183 url(../images/icon-search.png) 50% 50% no-repeat;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 60px;
      height: 60px;
      border-radius: 50%;
    }
  }

  // for homepage
  &-hp {

  }

}